@import "../../assets/scss/variables.scss";

.page {
  margin-top: $pageMarginTop;
  margin-bottom: 100px;
  .container {
    .modalEnter {
      opacity: 0;
    }
    .modalEnterActive {
      opacity: 1;
      transition: all 0.2s ease-in;
    }
    .modalExit {
      opacity: 1;
    }
    .modalExitActive {
      opacity: 0;
      transition: all 0.2s ease-out;
    }
    .clearCartButton {
      display: flex;
      gap: 5%;
      justify-content: center;
      align-items: center;
      float: right;
      width: 150px;
      height: 40px;
      border-radius: 10px;
      font-weight: 600;
      font-size: calc(16px + 2 * (100vw / 1200));
      background: none;
      transition: all 0.2s;
      &:hover {
        background: rgba(#000000, 0.05);
      }
    }
    .list {
      margin: calc(40px + 30 * (100vw / 1200)) 0px;
    }
    .total {
      .blockTitle {
        font-weight: 600;
        font-size: calc(16px + 2 * (100vw / 1200));
        line-height: normal;
      }
      .totalList {
        margin: 30px 0px;
        .totalListItem {
          margin-bottom: 30px;
          display: flex;
          align-items: center;
          gap: 20px;
          padding: 5px;
          font-weight: 500;
          font-size: calc(14px + 2 * (100vw / 1200));
          line-height: normal;
          color: #555;
          border-bottom: 1px dashed #555;
          .totalItemTitle {
            font-weight: 500;
          }
        }
      }
      .totalPrice {
        display: flex;
        gap: 20px;
        font-weight: 600;
        font-size: 16px;
      }
    }
  }
}
